.alignthecard {
  margin: 3rem auto;
  text-align: center;
  align-content: center;
  flex-direction: column;
}

.card:hover {
  transform: scale(1.1);
  box-shadow: 0 13px 40px -5px hsla(240, 30.1%, 28%, 0.12),
    0 8px 32px -8px hsla(0, 100%, 93%, 0.14),
    0 -6px 32px -6px hsla(0, 0%, 0%, 0.02);
}

.card {
  background: #fff;
  width: 15em;
  border-radius: 0.6em;
  margin: 1em;
  overflow: hidden;
  cursor: pointer;
  box-shadow: 0 13px 27px -5px hsla(240, 30.1%, 28%, 0.25),
    0 8px 16px -8px hsla(0, 0%, 0%, 0.3), 0 -6px 16px -6px hsla(0, 0%, 0%, 0.03);
  transition: all ease 200ms;
}
.card Image {
  width: 300;
  height: 250;
  border-radius: 250 / 2;
}
.card p {
  font-weight: bold;
}
